// Reset the default WordPress login styles.
.login {

	h1 {

		a {
			background: none;
			height: auto;
			line-height: 1;
			text-indent: 0;
			width: auto;
		}
	}

	// Style included logo.
	&__logo {
		height: auto;
		max-height: 100px;
		max-width: 100%;
	}
}